import { InspectorControls } from "@wordpress/block-editor";
import {
	PanelBody,
	ButtonGroup,
	Button,
	GradientPicker,
} from "@wordpress/components";
import { addFilter } from "@wordpress/hooks";
import { __ } from "@wordpress/i18n";
import { useMemo } from "@wordpress/element";
import { useSetting } from '@wordpress/block-editor';

import "./style.scss";

const allowedBlocks = ["core/paragraph", "core/heading", "core/quote"];

const customGradientPresets = [
	{
		name: "JShine",
		gradient: "linear-gradient(135deg,#12c2e9 0%,#c471ed 50%,#f64f59 100%)",
		slug: "jshine",
	},
	{
		name: "Cool Sky",
		gradient: "linear-gradient(135deg,#2980b9 0%,#6dd5fa 50%,#ffffff 100%)",
		slug: "cool-sky",
	},
	{
		name: "Lunada",
		gradient: "linear-gradient(135deg,#5433FF 0%,#20BDFF 51%,#A5FECB 100%)",
		slug: "lunada",
	},
	{
		name: "Blue Raspberry",
		gradient: "linear-gradient(135deg,#00B4DB 0%,#0083B0 100%)",
		slug: "blue-raspberry",
	},
	{
		name: "Citrus Peel",
		gradient: "linear-gradient(135deg,#FDC830 0%,#F37335 100%)",
		slug: "citrus-peel",
	},
	{
		name: "Sin City Red",
		gradient: "linear-gradient(135deg,#ED213A 0%,#93291E 100%)",
		slug: "sin-city-red",
	},
	{
		name: "Blue Skies",
		gradient: "linear-gradient(135deg,#56CCF2 0%,#2F80ED 100%)",
		slug: "blue-skies",
	},
	{
		name: "Mango Pulp",
		gradient: "linear-gradient(135deg,#F09819 0%,#EDDE5D 100%)",
		slug: "mango-pulp",
	},
	{
		name: "Frozen",
		gradient: "linear-gradient(135deg,#403B4A 0%,#E7E9BB 100%)",
		slug: "frozen",
	},
	{
		name: "Rose Water",
		gradient: "linear-gradient(135deg,#E55D87 0%,#5FC3E4 100%)",
		slug: "rose-water",
	},
	{
		name: "Moonlit Asteroid",
		gradient: "linear-gradient(135deg,#0F2027 0%, #203A43 0%, #2c5364 100%)",
		slug: "moonlit-asteroid",
	},
	{
		name: "Rastafarie",
		gradient: "linear-gradient(135deg,#1E9600 0%, #FFF200 0%, #FF0000 100%)",
		slug: "rastafari",
	},
];

const FilterBlocks = (settings) => {
	// If the block is not allowed, we return it unaltered.
	if (!allowedBlocks.includes(settings.name)) {
		return settings;
	}

	const newSettings = {
		...settings,
		attributes: {
			...settings.attributes, // spread in old attributes so we don't lose them!
			hasGradient: {
				// Here is our new boolean attribute to know if we are having a griadent or not.
				type: "boolean",
				default: false,
			},
			// Here is our new string attribute to hold the gradient colors.
			gradientColors: {
				type: "string",
				default: "linear-gradient(135deg,#12c2e9 0%,#c471ed 50%,#f64f59 100%)",
			},
			uid: {
				type: "string",
				default: "",
			},
		},

		edit(props) {
			const userGradientPalette = useSetting( 'color.gradients.custom' );
			const themeGradientPalette = useSetting( 'color.gradients.theme' );
			const defaultGradientPalette = useSetting( 'color.gradients.default' );
			const allGradients = useMemo(
				() => [
					...( customGradientPresets || [] ),
					...( defaultGradientPalette || [] ),
					...( themeGradientPalette || [] ),
					...( userGradientPalette || [] ),
				],
				[ userGradientPalette, themeGradientPalette, defaultGradientPalette ]
			);

			const { attributes, setAttributes } = props;
			const { hasGradient, gradientColors } = attributes;

			if (!props.attributes.uid) {
				const uniqueId = `gradient-text-${Math.floor(Math.random() * 100000)}`;
				props.setAttributes({ uid: uniqueId });
			}

			return (
				<>
					<InspectorControls>
						<PanelBody title={__("Gradient Text?")}>
							<ButtonGroup aria-label={__("Gradient Text?")}>
								{["Yes", "No"].map((has) => {
									return (
										<Button
											key={has}
											isSmall
											variant={
												hasGradient === (has === "Yes" ? true : false)
													? "primary"
													: undefined
											}
											onClick={() =>
												setAttributes({
													hasGradient: has === "Yes",
												})
											}
										>
											{has}
										</Button>
									);
								})}
							</ButtonGroup>
						</PanelBody>
						{hasGradient && (
							<PanelBody title={__("Choose Colors")}>
								<GradientPicker
									value={gradientColors}
									onChange={(currentGradient) =>
										setAttributes({ gradientColors: currentGradient })
									}
									gradients={allGradients}
								/>
							</PanelBody>
						)}
					</InspectorControls>
					{hasGradient ? (
						<div className={`is-gradient-text ${props.attributes.uid}`}>
							<style>
								{`
									.${props.attributes.uid} > * {
										-webkit-text-fill-color: transparent;
										background: ${gradientColors};
										-webkit-background-clip: text;
										-webkit-box-decoration-break: clone;
										color: black;
									}
								`}
							</style>
							{settings.edit(props)}
						</div>
					) : (
						settings.edit(props)
					)}
				</>
			);
		},
		save(props) {
			const { attributes } = props;
			const { hasGradient, gradientColors } = attributes;
			return hasGradient ? (
				<div className={`is-gradient-text ${props.attributes.uid}`}>
					<style>
						{`
							.${props.attributes.uid} > * {
								-webkit-text-fill-color: transparent;
								background: ${gradientColors};
								-webkit-background-clip: text;
								-webkit-box-decoration-break: clone;
								color: black;
							}
						`}
					</style>
					{settings.save(props)}
				</div>
			) : (
				settings.save(props)
			);
		},
	};

	return newSettings;
};

addFilter("blocks.registerBlockType", "gt/grident-text", FilterBlocks);
